import { ChangeDetectionStrategy, ChangeDetectorRef, Component, inject, OnInit, ViewChild } from '@angular/core';
import { merge, Observable, of, Subject } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { defaultLanguage, languages } from '../shared/model/languages';
import { SpeechError } from '../shared/model/speech-error';
import { SpeechEvent } from '../shared/model/speech-event';
import { SpeechRecognizerService } from '../shared/services/web-apis/speech-recognizer.service';
import { ActionContext } from '../shared/services/actions/action-context';
import { SpeechNotification } from '../shared/model/speech-notification';
import { HttpClient } from '@angular/common/http';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import {MatSnackBar} from '@angular/material/snack-bar';

interface User {
  sl: number;
  name: string;
  dob: string;
}


@Component({
  selector: 'wsa-web-speech',
  templateUrl: './web-speech.component.html',
  styleUrls: ['./web-speech.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class WebSpeechComponent implements OnInit {
  languages: string[] = languages;
  currentLanguage: string = defaultLanguage;
  totalTranscript?: string;
  modes: string[]= ['Save','Search']
  modeSelect:string='Save'
  transcript$?: Observable<string>;
  listening$?: Observable<boolean>;
  errorMessage$?: Observable<string>;
  defaultError$ = new Subject<string | undefined>();

  constructor(
    private speechRecognizer: SpeechRecognizerService,
    private actionContext: ActionContext,
private http:HttpClient, private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.getDetails();
    const webSpeechReady = this.speechRecognizer.initialize(this.currentLanguage);
    if (webSpeechReady) {
      this.initRecognition();
    }else {
      this.errorMessage$ = of('Your Browser is not supported. Please try Google Chrome.');
    }
  }

  start(): void {
    if (this.speechRecognizer.isListening) {
      this.stop();
      return;
    }

    this.defaultError$.next(undefined);
    this.speechRecognizer.start();
  }

  stop(): void {
    this.totalTranscript=undefined
    this.speechRecognizer.stop();
  }

  selectMode(mode:string): void {
    if (this.speechRecognizer.isListening) {
      this.stop();
        }
      this.speechRecognizer.setLanguage(this.currentLanguage);
      this.modeSelect= mode

  }
  displayedColumns: string[] = [ 'name', 'dob', "pan",
    "city",
    "pincode",
    "address"];
  dataSource = new MatTableDataSource<any>(); // Use MatTableDataSource for pagination

  @ViewChild(MatPaginator) paginator!: MatPaginator;
	public array: any;
  
  
getDetails(){
  this.cdr.detectChanges();

  this.http.get("http://localhost:8089/client/getClientDetails").subscribe( (x:any)=>{
    this.totalTranscript=undefined
    this.dataSource.data=x
    this.dataSource.paginator=this.paginator
    this.array = this.dataSource.data;
    this.totalSize = this.array.length;
    this.cdr.detectChanges();

    console.log( this.dataSource)

  }) 
}
public pageSize = 5;
  public currentPage = 0;
  public totalSize = 0;
onPaginateChange(event:any) {
  if (this.currentPage !== event.pageIndex) {
    console.log(event.pageIndex);
    this.currentPage = event.pageIndex;
    this.pageSize =event.pageSize
  }
  }

reset(){
  this.totalTranscript = ''
}
private _snackBar = inject(MatSnackBar);


  postMessage(){ 
  if(this.modeSelect === 'Save'){
  this.http.post<string>("http://localhost:8089/client/saveClientDetails",this.totalTranscript,{ responseType: 'text' as 'json' }).subscribe( (x:any)=>{
    console.log(x)

    if(x==="Data saved successfully."){
      this.totalTranscript=undefined
      if (this.speechRecognizer.isListening) {
        this.stop();
          }
    }else if(x === 'Data is unclear, unable to parse date'){

      this._snackBar.open('Input recognition error', 'cancel');

    }
    this.getDetails()
  })
}else{
  this.http.post("http://localhost:8089/client/fetchClientDetails",this.totalTranscript).subscribe( (x:any)=>{
    this.totalTranscript=undefined
    this.dataSource.data=x
    this.dataSource.paginator=this.paginator
    this.array = this.dataSource.data;
    this.totalSize = this.array.length;
    this.cdr.detectChanges();

    console.log( this.dataSource)
  })

}
  }


  private initRecognition(): void {
    this.transcript$ = this.speechRecognizer.onResult().pipe(
      tap((notification) => {
        this.processNotification(notification);
      }),
      map((notification) => notification.content || '')
    );

    this.listening$ = merge(
      this.speechRecognizer.onStart(),
      this.speechRecognizer.onEnd()
    ).pipe(map((notification) => notification.event === SpeechEvent.Start));

    this.errorMessage$ = merge(
      this.speechRecognizer.onError(),
      this.defaultError$
    ).pipe(
      map((data) => {
        if (data === undefined) {
          return '';
        }
        if (typeof data === 'string') {
          return data;
        }
        let message;
        switch (data.error) {
          case SpeechError.NotAllowed:
            message = `Cannot run the demo.
            Your browser is not authorized to access your microphone.
            Verify that your browser has access to your microphone and try again.`;
            break;
          case SpeechError.NoSpeech:
            message = `No speech has been detected. Please try again.`;
            break;
          case SpeechError.AudioCapture:
            message = `Microphone is not available. Plese verify the connection of your microphone and try again.`;
            break;
          default:
            message = '';
            break;
        }
        return message;
      })
    );
  }

  private processNotification(notification: SpeechNotification<string>): void {
    if (notification.event === SpeechEvent.FinalContent) {
      const message = notification.content?.trim() || '';
      this.actionContext.processMessage(message, this.currentLanguage);
      // this.actionContext.runAction(message, this.currentLanguage);
      this.totalTranscript = this.totalTranscript
        ? `${this.totalTranscript}\n${message}`
        : notification.content;
       //s this.postMessage()
    }
  }
  private speechSynthesis = window.speechSynthesis;

  readData(): void {
   const  dataToRead =this.dataSource.data
      .map(row => ` Name: ${row.name}, Date of Birth: ${row.dob}, Pan: ${row.pan}
        , City: ${row.city}, Address: ${row.address}, Pincode: ${row.pincode}`)
      .join('. ');
    this.speak(dataToRead);
  }

 speak(text: string): void {
    if (this.speechSynthesis.speaking) {
      console.warn('SpeechSynthesis is already speaking.');
      return;
    }
    const utterance = new SpeechSynthesisUtterance(text);
    this.speechSynthesis.speak(utterance);
  }

  stopSpeech(): void {
    if (this.speechSynthesis.speaking) {
      this.speechSynthesis.cancel();
    }
  }
}
